<template>
  <body class="d-flex flex-column" v-if="flag">
    <!-- <div>Form: {{ voucher_type }}</div> -->

    <v-dialog v-model="dialog" persistent width="300">
      <v-card color="primary" dark class="px-4 pt-2">
        <v-card-text>
          Loading ...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogTaC" width="600px">
      <v-card>
        <v-card-title
          class="py-4 mb-4 justify-center align-center"
          style="text-align: center !important"
        >
          <div
            style="font-size: 20px !important; text-align: center !important"
