            KEBIJAKAN DATA PRIBADI (KEBIJAKAN PRIVASI)
          </div>
          <div
            style="font-size: 20px !important; text-align: center !important"
          >
            APLIKASI ALFAGIFT
          </div>
        </v-card-title>

        <v-card-text style="text-align: justify !important">
          <p style="text-align: justify !important">
            Selamat datang di Program Promosi Alfagift “Precise” yang
            diselenggarakan oleh PT GLOBAL LOYALTY INDONESIA.
          </p>

          <p style="text-align: justify !important">
            Kebijakan Privasi di bawah merupakan perjanjian antara Anda dengan
            Kami yang mengatur Kebijakan Privasi atas keikutsertaan Anda pada
            Program Precise ini.
          </p>

          <p style="text-align: justify !important">
            Sebelum mengakses dan/atau mendaftarkan diri dan/atau mengikuti
            Program Precise, Anda wajib membaca, memahami, mematuhi dan setuju
            untuk tunduk serta terikat dengan Kebijakan Privasi ini terlebih
            dahulu, termasuk perubahannya.
          </p>

          <p style="text-align: justify !important">
            Dengan mengakses dan/atau mendaftarkan data diri Anda dan/atau
            mengikuti Program Precise ini, Anda dengan ini menyadari dan tanpa
            paksaan pihak manapun menyatakan setuju untuk menerima dan terikat
            dengan Kebijakan Privasi ini termasuk perubahannya. Jika Anda tidak
            menyetujui Kebijakan Privasi ini termasuk perubahannya, agar Anda
            tidak atau berhenti mengakses dan/atau mengikuti Program Precise
            ini.
          </p>

          <p style="text-align: justify !important">
            Kebijakan Privasi ini dapat Kami ubah sewaktu-waktu dan akan
            diberitahukan melalui sarana media komunikasi milik Kami. Anda
            bertanggung jawab untuk tetap mengetahui update atau pembaharuan
            Kebijakan Privasi ini.
          </p>

          <p style="text-align: justify !important">
            Keputusan yang diambil berdasarkan Kebijakan Privasi ini termasuk
            perubahannya bersifat mutlak dan tidak dapat diganggu gugat dan
            mengikat Anda selama Anda mengikuti Program Precise.
          </p>

          <p style="text-align: justify !important; font-weight: bold">
            A. Perolehan dan Pengumpulan Data Pribadi
          </p>

          <p style="text-align: justify !important">
            Data Pribadi (“Data Anda”) adalah setiap data yang diberikan Anda
            kepada Kami dalam rangka keikutsertaan pada Program Precise termasuk
            namun tidak terbatas pada :
          </p>

          <div style="display: flex">
            <p style="display: inline-block; width: 100px"></p>
            <p>
              <span
                >1. Data yang Anda berikan secara langsung pada saat
                mendaftarkan keikutsertaannya pada Program Precise ini, Anda
                akan memberikan data Anda kepada Kami termasuk namun tidak
                terbatas pada :</span
              >
            </p>
          </div>

          <div style="display: flex">
            <p style="display: inline-block; width: 100px"></p>
            <p style="display: inline-block; width: 10px"></p>
            <p class="ma-0 pa-0">
              <strong>(1) nama;</strong>
            </p>
          </div>
          <div style="display: flex">
            <p style="display: inline-block; width: 100px"></p>
            <p style="display: inline-block; width: 10px"></p>
            <p class="ma-0 pa-0">
              <strong> (2) alamat pengiriman;</strong>
            </p>
          </div>
          <div style="display: flex">
            <p style="display: inline-block; width: 100px"></p>
            <p style="display: inline-block; width: 10px"></p>
            <p class="ma-0 pa-0">
              <strong> (3) alamat email;</strong>
            </p>
          </div>
          <div style="display: flex">
            <p style="display: inline-block; width: 100px"></p>
            <p style="display: inline-block; width: 10px"></p>
            <p>
              <strong>(4) nomor handphone.</strong>
            </p>
          </div>

          <div style="display: flex">
            <p style="display: inline-block; width: 100px"></p>
            <p>
              <span
                >2. Data Anda yang dibaca dan/atau dikumpulkan ketika Anda
                mengikuti Program Precise yaitu data teknis Anda yaitu alamat IP
                untuk menjalankan fungsi dari Program Precise.</span
              >
            </p>
          </div>

          <p style="text-align: justify !important; font-weight: bold">
            B. Penggunaan Data Pribadi
          </p>

          <p style="text-align: justify !important">
            1. Anda dengan ini setuju untuk memberikan izin kepada Kami untuk
            memperoleh, mengumpulkan, melakukan verifikasi, menggunakan,
            memanfaatkan, menampilkan, mengumumkan, mengirimkan, memproses,
            menyimpan, memberikan, membagikan, mengolah, mengelola, memusnahkan,
            melindungi, dan/atau melakukan kerjasama dengan pihak ketiga
            penyedia layanan teknologi informasi atas Data Anda untuk tujuan :
          </p>

          <div style="display: flex">
            <p style="display: inline-block; width: 100px"></p>
            <p style="display: inline-block; width: 10px"></p>
            <div>
              <p>a. Memfasilitasi implementasi Program Precise;</p>
              <p>
                b. Menanggapi pertanyaan, tanggapan, klaim atau sengketa, baik
                secara langsung atau melalui agen layanan pelanggan Kami yang
                dialihdayakan <i>(outsourced)</i>;
              </p>
              <p>
                c. Mengirimkan produk barang kepada Anda. Kami dapat
                menyampaikan informasi pribadi Anda kepada Mitra untuk melakukan
                pengiriman produk barang kepada Anda (misalnya kepada kurir atau
                pemasok Kami);
              </p>

              <p>
                d. Mengirimkan komunikasi via email/whatsapp/sms/telepon dari
                Kami kepada Anda mengenai Program Precise dan program lain yang
                relevan.
              </p>
              <p>
                e. Membandingkan informasi, dan melakukan verifikasi dengan
                pihak ketiga guna memastikan bahwa informasi tersebut akurat;
              </p>
              <p>
                f. Memastikan identitas Anda dengan tujuan identifikasi penipuan
                (memverifikasi kepemilikan Anda atas informasi yang Anda
                berikan);
              </p>
              <p>
                g. Melakukan penelitian tentang demografi dan perilaku Anda
                selaku konsumen Kami;
              </p>
              <p>
                h. Memberikan kepada Anda informasi yang menurut Kami dapat
                berguna untuk Anda, termasuk informasi tentang Produk dan
                layanan Mitra, sepanjang Anda telah mengindikasikan bahwa Anda
                tidak keberatan dihubungi untuk tujuan ini;
              </p>
              <p>
                i. Menampilkan nama atau profil Anda untuk keperluan pelaksanaan
                Program Precise;
              </p>
              <p>
                j. Menyediakan informasi yang lebih ditargetkan kepada Anda
                <i>(Promote Targeted)</i>.
              </p>
              <p>
                k. Mengirimkan materi pengiklanan atau promosi atas Produk dan
                Layanan Kami dan/atau Mitra dari waktu ke waktu (kecuali Anda
                memilih untuk tidak menerima materi ini);
              </p>
            </div>
          </div>

          <p style="text-align: justify !important">
            2. Kami juga dapat melakukan proses pengambilan keputusan otomatis
            sesuai dengan setiap tujuan-tujuan sebagaimana ketentuan B.1 di
            atas.
          </p>

          <p style="text-align: justify !important">
            3. Anda dan Kami wajib menjaga kerahasiaan dan keamanan dari setiap
            Data Anda yang dikirimkan dan/atau disimpan untuk Program Precise,
            kecuali untuk keperluan sebagaimana dimaksud dalam ketentuan B.1 di
            atas.
          </p>

          <p style="text-align: justify !important">
            4. Kami tidak bertanggung jawab atas kerugian atau kerusakan yang
            timbul dari atau berhubungan dengan kehilangan atau pencurian Data
            Anda yang disebabkan oleh kelalaian atau pelanggaran keamanan yang
            dilakukan oleh Anda selaku pemilik Data Anda itu sendiri.
          </p>

          <p style="text-align: justify !important">
            5. Anda dapat memberitahukan permintaan Anda kepada Kami, untuk
            menghapus Data Anda (baik data-data tertentu atau keseluruhan data
            Anda), dalam hal itu, Anda dapat mengajukan permintaan penghapusan
            data dengan cara email ke
            <a style="font-color: blue; text-decoration: underline"
              >alfacare@sat.co.id</a
            >. Kami dapat mengabulkan permintaan tersebut sejauh diizinkan
            berdasarkan ketentuan perundang-undangan yang berlaku.
          </p>
        </v-card-text>

        <v-card-actions class="pa-4">
          <v-spacer></v-spacer>
          <!--   <v-btn color="green darken-1" text @click="dialog = false">
            Disagree
          </v-btn> -->
          <v-btn
            color="green darken-1"
            text
            @click="dialogTaC = false"
            style="font-weight: bold !important"
          >
            Setuju
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-form class="px-3" style="max-width: 500px !important">
      <h2 class="my-4">Get Voucher</h2>
      <v-container width="100%" fluid>
        <v-row width="100%" class="d-flex justify-center">
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-text-field
              label="Name"
              v-model="input.name"
              :rules="textInputRules"
              outlined
              required
              width="100%"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0">
            <v-text-field
              label="Email"
              v-model="input.email"
              :rules="emailRules"
              outlined
              required
              width="100%"
              hint="**pastikan alamat email anda benar karena voucher akan dikirimkan lewat email."
              persistent-hint
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12" width="100%" class="ma-0 pa-0 mt-3">
            <v-text-field
              label="Phone"
              v-model="input.phone"
              :rules="phoneNumberRules"
              outlined
              required
              width="100%"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row width="100%">
          <v-col
            width="100%"
            class="d-flex justify-start align-center pa-0 mt-16 mb-8"
          >
            <v-checkbox
              class="ma-0 pa-0"
              v-model="checkbox"
              color="success"
              hide-details
            ></v-checkbox>
            <!-- <div style="font-size: 12px !important; letter-spacing: 0.6px">
              I agree to receive communications via Email/ Call/ Whatsapp from
              <a target="_blank" href="https://gli.id/">
                PT. Global Loyalty Indonesia
              </a>
              (Alfa Group) about this programme and other relevant programmes.
            </div> -->

            <div
              class="ma-0 pa-0 mt-auto"
              style="font-size: 12px !important; letter-spacing: 0.6px"
            >
              Saya telah membaca dan setuju syarat dan ketentuan terkait
              <a
                @click="dialogTaC = true"
                style="font-color: blue; text-decoration: underline"
              >
                Kebijakan Data Pribadi/Kebijakan Privasi</a
              >
              ini
            </div>
          </v-col>
        </v-row>

        <v-row width="100%" class="d-flex justify-start">
          <v-btn
            class="white--text"
            width="100%"
            height="45px"
            block
            color="#f77a4a"
            @click="onSubmit()"
            >Get The Voucher</v-btn
          >
        </v-row>
      </v-container>
    </v-form>
  </body>
  <body v-else>
    <div class="loader"></div>
  </body>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import { api_url } from "../global_variable/global_variable.js";
import sha256 from "js-sha256";

export default {
  name: "Form",

  components: {},

  data() {
    return {
      flag: false,
      dialog: false,
      dialogTaC: false,
      pe_id: null,
      voucher_type: null,
      pe_end_date: null,
      subject_mail: null,
      checkbox: false,
      input: {
        name: null,
        email: null,
        phone: "08",
      },
      client_ip: 0,

      textInputRules: [(v) => !!v || "Kolom harus diisi"],
      emailRules: [
        (v) =>
          (!!v && /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v)) ||
          "E-mail tidak valid",
      ],
      phoneNumberRules: [
        (v) =>
          (v.length >= 10 &&
            String(v).substring(0, 1) == "0" &&
            /^\d+$/.test(v)) ||
          "Nomor Hp tidak valid",
      ],
      rules: {},
    };
  },

  created() {},

  async mounted() {
    if (!localStorage.voucher_token) {
      this.$router.push({ name: "notFound" });
    } else if (
      localStorage.client_ip == "" ||
      localStorage.client_ip == 0 ||
      localStorage.client_ip == null ||
      localStorage.client_ip == undefined
    ) {
      this.$router.push({ name: "ip-pernah-digunakan" });
    } else {
      localStorage.removeItem("date_end");
      localStorage.removeItem("voucher_token");
      this.pe_id = localStorage.pe_id;
      this.voucher_type = localStorage.voucher_type;
      this.pe_end_date = localStorage.pe_end_date;
      this.subject_mail = localStorage.subject_mail;
      this.client_ip = localStorage.client_ip;
      localStorage.removeItem("pe_id");
      localStorage.removeItem("voucher_type");
      localStorage.removeItem("pe_end_date");
      localStorage.removeItem("subject_mail");
      localStorage.removeItem("client_ip");

      try {
        var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
        var tgl = dateNow.toString();
        var kunci = "SSO" + "4lfag1ft" + tgl + "precise";
        // console.log(kunci);
        var token = sha256(kunci);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .get(
            api_url + "/check-voucher?token=" + token + "&pe_id=" + this.pe_id
          )
          .then((response) => {
            // console.log(response.data);
            if (response.data.response_message != "success") {
              this.$router.push({ name: "voucher-habis" });
              localStorage.removeItem("voucher_token");
              localStorage.removeItem("pe_id");
              localStorage.removeItem("voucher_type");
              localStorage.removeItem("pe_end_date");
              localStorage.removeItem("subject_mail");
              localStorage.removeItem("client_ip");
            } else {
              this.checkIp();
            }
          })
          .catch((error) => {
            console.log("Form.vue (error) :: ", error);
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }
    }
  },

  methods: {
    async checkIp() {
      // console.log(this.client_ip);
      try {
        var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
        var tgl = dateNow.toString();
        var kunci = "SSO" + "4lfag1ft" + tgl + "precise";
        // console.log(kunci);
        var token = sha256(kunci);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .get(
            api_url +
              "/check-ip?token=" +
              token +
              "&pe_id=" +
              this.pe_id +
              "&client_ip=" +
              this.client_ip,
            headers
          )
          .then((response) => {
            // console.log(response.data);
            if (response.data.response_message != "success") {
              this.$router.push({ name: "ip-pernah-digunakan" });
            } else if (response.data.data.ip_status == true) {
              this.$router.push({ name: "ip-pernah-digunakan" });
            } else {
              // Pass all validation and check
              this.flag = true;
            }
          })
          .catch((error) => {
            console.log("Form.vue (error) :: ", error);
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }
    },
    async onSubmit() {
      // console.log(this.checkbox);
      if (
        this.checkbox &&
        this.input.name != null &&
        this.input.email != null &&
        this.input.phone != null &&
        this.ValidateEmail(this.input.email) &&
        this.ValidatePhone(this.input.phone)
      ) {
        this.dialog = true;
        var username =
          this.input.name[0].toUpperCase() + this.input.name.slice(1);
        // console.log(username, this.input.email, this.input.phone);
        // console.log(this.voucher_type);
        try {
          var dateNow = new Date().toISOString().slice(0, 10).replace(/-/g, "");
          var tgl = dateNow.toString();
          var kunci =
            "SSO" +
            "4lfag1ft" +
            tgl +
            "precise" +
            this.input.email.toLowerCase();
          // console.log(kunci);
          var token = sha256(kunci);

          // console.log(this.$route.params.router_data);

          var body = {
            type: this.voucher_type,
            pe_id: this.pe_id,
            name: username,
            email: this.input.email.toLowerCase(),
            phone: this.input.phone,
            body_html: this.$route.params.router_data,
            subject_html: this.subject_mail,
            client_ip: this.client_ip,
          };

          const headers = {
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Credentials": "true",
            "Access-Control-Allow-Methods":
              "GET, PUT, POST, DELETE, OPTIONS, post, get",
            "Access-Control-Allow-Headers":
              "Origin, Content-Type, X-Auth-Token",
            "Content-Type": "application/json",
            "Content-Length": "<calculated when request is sent>",
            Host: "<calculated when request is sent>",
            Accept: "*/*",
            "Accept-Encoding": "gzip, deflate, br",
            Connection: "keep-alive",
            app: "sso",
          };
          // console.log(body);
          await axios
            .post(api_url + "/assign-voucher?token=" + token, body, headers)
            .then((response) => {
              // console.log(response.data);
              if (response.data.response_message == "success") {
                this.$router.push({ name: "voucher" });
                localStorage.removeItem("voucher_token");
                localStorage.removeItem("pe_id");
                localStorage.removeItem("voucher_type");
                localStorage.removeItem("pe_end_date");
                localStorage.removeItem("subject_mail");
                localStorage.removeItem("client_ip");
              } else if (
                response.data.response_message.includes(
                  "Error : duplicate key value violates unique constraint"
                )
              ) {
                this.$router.push({ name: "email-pernah-digunakan" });
                localStorage.removeItem("voucher_token");
                localStorage.removeItem("pe_id");
                localStorage.removeItem("voucher_type");
                localStorage.removeItem("pe_end_date");
                localStorage.removeItem("subject_mail");
                localStorage.removeItem("client_ip");
              } else {
                alert(
                  "Gagal mengirim kode voucher. Silahkan coba beberapa saat lagi."
                );
              }
            })
            .catch((error) => {
              console.log("Form.vue (error) :: ", error);
            });
        } catch (e) {
          console.error(e);
          alert("Error: koneksi gagal");
        }
      } else {
        alert(
          "Silahkan melengkapi isi form dan menyetujui Terms and Conditions"
        );
      }

      this.dialog = false;
    },
    ValidateEmail(mail) {
      if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail)) {
        return true;
      }
      alert("Email tidak valid!");
      return false;
    },
    ValidatePhone(phone) {
      if (/^\d+$/.test(phone)) {
        return true;
      }
      alert("Nomor Hp tidak valid!");
      return false;
    },
  },
};
</script>

<style scoped>
body {
  /* background-color: #073e5d; */
  background-size: cover;
  /* font-family: Montserrat; */
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  border: 8px solid #f3f3f3;
  border-radius: 50%;
  border-top: 8px solid #3498db;
  width: 60px;
  height: 60px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>
